import React from 'react'
import { PrinterOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Button } from 'antd';
import { useHistory } from "react-router-dom";

const { Column } = Table; 
const Packing = (props) => { 	
	
	const { receiptTotal, receiptId, orderDetails, salesInfo, shopName, review, userData, handleCloseParking, shortTypes, getId} = props;

	const getDetails = (record) => {
		let {pkgUnit,qtyPkg,ctnSqm,productType, quantity, purchasedType} = record;
		let qty = purchasedType === '0' ? quantity : (purchasedType === '1' ? quantity * qtyPkg : (parseInt(purchasedType) > 4 ? parseInt(quantity * ctnSqm) :(pkgUnit === '1' ? parseInt(quantity * (qtyPkg/ctnSqm)) : parseFloat(quantity / ctnSqm))));
		let sqMeters = (productType ===  '1' && pkgUnit ===  '1') ? qty*(ctnSqm/qtyPkg): (productType ===  '1' && pkgUnit ===  '0') ? qty*ctnSqm : 0;
		let meters = (productType ===  '2' && pkgUnit ===  '1') ? qty*ctnSqm*qtyPkg: (productType ===  '2' && pkgUnit ===  '0') ? qty*ctnSqm : 0;
		let kilos = (productType ===  '3' && pkgUnit ===  '1') ? qty*ctnSqm*qtyPkg: (productType ===  '3' && pkgUnit ===  '0') ? qty*ctnSqm : 0;
		let cartons = pkgUnit ===  '1' ? parseInt(qty/qtyPkg) : 0;
		let remaining = qty - (cartons * qtyPkg);
		let others = parseInt(productType) > 3 ? parseInt(remaining / ctnSqm) + ' ' + shortTypes[productType] + ' ': '';
		others = (parseInt(productType) > 3 && pkgUnit ===  '1') ? 'and ' + others: others;
		let pieces = parseInt(productType) > 3 ? parseInt(remaining % ctnSqm) : parseInt(remaining);
		let result = pkgUnit ===  '1' ? `${cartons} Cts ${others}and ${pieces} ${shortTypes[0]}` : `${others}${pieces} ${shortTypes[0]}`;
		let meter = productType === '2' ? ' and ' + parseInt(meters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let sqMeter = (productType === '1' && pkgUnit ===  '0') ? ' and ' + parseInt(sqMeters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let kg = productType === '3' ? ' and ' + parseInt(kilos % ctnSqm) +' '+ shortTypes[productType] : ''; 
		result +=  meter + sqMeter + kg;
		return result;
	}

	return (
		<div className="container">
			<Card>
				<div className="w-100 text-center">
					<div>
						<img src="/img/logo.png" alt=""/>
						<h2 className="mb-1 font-weight-semibold">Packing List #{getId(salesInfo._id)}</h2>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{salesInfo.customerName}</span><br />
							</p>
						</address>
					</div>
				</div>
				<div className="mt-1">
					<Table dataSource={orderDetails} pagination={false} className="mb-5">
						<Column title="No." dataIndex="key" key="key" render={(_,__,index) => index+1}/>
						<Column title="Product" dataIndex="product" key="product" render={(_,record) => (record.productName) ? record.product : record.data().product}/>
						<Column title="Quantity" dataIndex="quantity" key="quantity" render={(_,record) => (record.productName) ? getDetails(record) : getDetails(record.data()) }/>
					</Table>
					<div className="d-flex justify-content-center">
						<div className="text-center ">
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Thank You! </span>
							</h2>
							<address>
								<p>
									<span>Done by: {salesInfo.backupUser}</span><br />
									<span>{receiptId.toLocaleString()}</span><br />
								</p>
							</address>
						</div>
					</div>
				</div>
				<hr className="d-print-none"/>
				{ !review && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => handleCloseParking()}>
						<CloseCircleOutlined /> Close
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>)}
				{(review && salesInfo.refunded === false) && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => handleCloseParking()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>)}
				{(review && salesInfo.source === userData.userStore && salesInfo.refunded === 'Pending') && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => handleCloseParking()}>
						<CloseCircleOutlined type="printer"/> Close
					</Button>
				</div>)}

			</Card>
		</div>
	);
}

export default Packing
