import React,{ useState } from 'react'
import { Card, Button, message } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import { PlusCircleOutlined } from '@ant-design/icons';
import { useHistory } from "react-router-dom";
import { DataService } from "services/sales.service";
import List from './list';
import Invoice from '../invoice';

const { Title } = Typography;
const Purchases = ({systemData}) => {
	const { sales, salesLoading, records, recordsLoading, shops, shopsLoading, userData, shortTypes, methodColors, users, userLoading, avatarColors, getId} = systemData;
	const [receiptTotal, setReceiptTotal] = React.useState(0);
	const [receiptId, setReceiptId] = React.useState();
	const [orderDetails, setOrderDetails] = useState([]);
	const [salesInfo, setSalesInfo] = React.useState({});
	const [shopName, setShopName] = React.useState({});
	const [visible, setVisible] = React.useState(false);
	let history = useHistory();	
	const addSale = () => {
		history.push(`/app/apps/sales/add`)
	}	
	
	const showInvoice = (employee) => {
		let recId = new Date(+employee._id);
		let orderDetails = records.docs.filter(x=> x.data().saleId === +employee._id);
		setOrderDetails(orderDetails);
		setReceiptId(recId);
		setReceiptTotal(employee.amount)
		setShopName((shops[employee.source]) ? shops[employee.source] : {'name':employee.backupSource,'location':employee.backupLocation})
		setSalesInfo(employee);
		setVisible(true);
	};

	const sendRequest = (data) => {
		data.refunded = 'Pending'
		DataService.update(data._id,data);
		setVisible(false);
		message.success("Refund request sent!");
	};

	return (
		<>
		<Card className={(visible) ? 'd-none' : ''}>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3">
						<Title level={4}>Sold Products</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={addSale} size="small" type="primary" icon={<PlusCircleOutlined />} block>Add New</Button>
				</div>
			</Flex>
			<List products={salesLoading ? [] : sales.docs.reverse()} loading={salesLoading} stores={shops} sloading={shopsLoading} showInvoice={showInvoice} loadingRecord={recordsLoading} methodColors={methodColors} users={users} userLoading={userLoading}  avatarColors={avatarColors}/>
		</Card>
		{visible &&(<Invoice receiptTotal={receiptTotal} receiptId={receiptId} orderDetails={orderDetails} salesInfo={salesInfo} shopName={shopName} review={true} userData={userData} sendRequest={sendRequest} shortTypes={shortTypes} getId={getId}/>)}
		</>
	)
}
export default Purchases
